// Вариант Б. Включите части Bootstrap

// 1. Сначала включите функции (чтобы Вы могли управлять цветами, SVG, вычислением и т.д.)
@import 'node_modules/bootstrap/scss/functions';

// 2. Включите сюда любые переопределения переменных по умолчанию

// Bootstrap compatible gutter variable => $gutter
$grid-gutter-width: 1.5rem; //24px
$gutter: $grid-gutter-width;

// Grig
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);
$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1320px,
);

// Ratio
$aspect-ratios: (
  '1x1': 100%,
  '4x3': calc(3 / 4 * 100%),
  '16x9': calc(9 / 16 * 100%),
  '16x10': calc(10 / 16 * 100%),
  '21x9': calc(9 / 21 * 100%),
);

:root {
  --bs-body-bg: #fff;
  --bs-body-text-align: left;
  --bs-dark-rgb: 37, 37, 38;
}

// 3. Включите оставшуюся часть необходимых таблиц стилей Bootstrap
